.advantages {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    top: -124px;
    left: 0;
    width: 1170px;
    height: 391px;
    background-image: url("../../img/advantages/clouds-left.png");
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 3;
  }

  &::before {
    position: absolute;
    content: "";
    top: 62px;
    right: -6.5%;
    width: 788px;
    height: 263px;
    background-image: url("../../img/advantages/clouds-right.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    z-index: -33;
  }

  @include vp-1199 {
    &::after {
      display: none;
    }

    &::before {
      display: none;
    }
  }
}

.advantages__container {
  position: relative;
  padding: 117px 120px 216px;
  overflow: hidden;

  @include vp-1199 {
    padding: 48px 44px 87px;
  }

  @include vp-767 {
    padding: 11px 15px 66.5px 15px;
    margin: 0;
  }
}

.advantages__swiper {
  @include vp-1199 {
    width: 100%;
  }
}

.advantages__wrapper {
  position: relative;

  @include vp-1199 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 27px;
    width: 100%;
  }

  @include vp-767 {
    row-gap: 14.5px;
  }
}

.advantages__container h2 {
  position: relative;
  width: 294px;
  margin: 0 auto 45px auto;
  padding-left: 2px;
  font-size: 35px;
  font-weight: 800;
  line-height: 35px;

  @include vp-1199 {
    margin: 0 auto 46px auto;
    text-align: center;
    font-size: 25px;
    line-height: 25px;
  }

  @include vp-767 {
    width: 185px;
    padding: 0;
    margin: 0 auto 30px;
    font-size: 22px;
    line-height: 22px;
  }
}

.advantages__container h2 span {
  position: absolute;
  content: "";
  bottom: 0%;
  left: -27%;
  font-size: 55px;
  font-weight: 800;
  line-height: 55px;
  opacity: 0.03;

  @include vp-1199 {
    left: -6%;
    bottom: -7%;
    font-size: 40px;
    line-height: 40px;
  }

  @include vp-767 {
    display: none;
  }
}

.advantages__button {
  position: absolute;
  top: 114%;
  left: 46.8%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  @include vp-1199 {
    display: none;
  }
}

.advantages__swiper-next {
  width: 37px;
  height: 37px;
  background-color: $color-navy-blue;
  z-index: 5;
  margin-left: 4px;

  @include vp-1199 {
    display: none;
  }

  svg {
    display: block;
    margin: 13px auto;
  }
}

.advantages__swiper-prev {
  width: 37px;
  height: 37px;
  background-color: $color-navy-blue;

  @include vp-1199 {
    display: none;
  }

  svg {
    display: block;
    margin: 13px auto;
  }
}
